$column-slider-width: 730px;

.services {
    padding-bottom: 150px;
    &__container {
        @extend %flex;
        flex-direction: column; }

    &__header {
        margin: {
            top: 120px;
            bottom: 44px; } }

    &__select-type {
        margin: {
            left: $noclip;
            right: $noclip; }
        column-gap: 47px;
        justify-content: center;

        @media (max-width: 1200px) {
            flex-wrap: wrap;
            max-width: 600px;
            row-gap: 20px; }

        @media (max-width: 425px) {
            column-gap: 35px; } }

    &__menu-btn {
        cursor: pointer;
        margin: 0;
        padding: 0;
        background: 0;
        border: 0;
        opacity: 0.5;
        transition: $transition-default;
        &:focus, &:hover, &:active, &_active {
            opacity: 1; }

        &:hover, &:active {
            transform: scale(1.1); } }

    &__card, &__thumb-image {
        overflow: hidden; }

    &__card {
        width: 350px;
        height: 385px;
        background-color: $image-holder;
        margin: 0;
        padding: 0;

        @media (max-width: 1600px) {
            width: 100%;
            height: 20vw; }

        img {
            max-height: 100%; }

        &-info {
            position: relative;
            top: 79%;
            padding-left: 26px;
            color: $primary;
            z-index: 1;

            &-container {
                position: relative;
                height: 100%;
                width: 100%;
                bottom: 101%;
                opacity: 0;
                transition: $transition-default;
                &:hover {
                    opacity: 1; }

                @media (max-width: 1600px) {
                    bottom: 110%; }

                @media (max-width: 1440px) {
                    display: none; } } }

        &-category {
            cursor: default;
            font: {
                size: 12px;
                weight: 400; }
            text-transform: uppercase;
            padding: {
                top: 8px;
                bottom: 8px;
                left: 14px;
                right: 14px; }
            margin-bottom: 11px;
            max-width: max-content;

            &:hover {
                background-color: $accent-bright; } }

        &-link {
            @extend  %flex;
            gap: 14px; }

        &-overlay {
            width: 120%;
            height: 50%;
            background-color: $accent;
            position: relative;
            top: 64%;
            right: 10%;
            z-index: 0;
            filter: blur(20px) alpha(0.6); } }

    &__thumb-image {
        height: 100%;
        img {
            height: 100%;

            @media (max-width: 1600px) {
                width: 100%; } }

        @media (max-width: 1600px) {
            max-width: 96%; } }


    //Sliders

    &__slider {
        &-container {
            height: 860px;
            margin-top: 62px;
            @extend %flex;
            gap: 30px;

            @media (max-width: 1600px) {
                flex-direction: column;
                height: unset; } }

        &-buttons {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 23px;
            width: $column-slider-width - 7px; //due to swiper's limitations there's a small gap of empty space we need to take into account.
            height: 40px;
            z-index: 2;

            @media (max-width: 1600px) {
                max-width: 100%; }

            & button {
                @extend %flex;
                justify-content: center;
                border: 0px solid $primary;
                background-color: $secondary;
                transition: $transition-default;
                cursor: pointer;

                &:hover:enabled {
                    background-color: darken($secondary, 4%); }

                &:disabled {
                    cursor: default;
                    img {
                        opacity: 0.5; } } } } }


    //Custom swiper properties for propper display

    .swiper {

        &-column-container {
            height: 100%;
            max-width: $column-slider-width;
            @extend %flex;
            flex-direction: column;

            @media (max-width: 1600px) {
                max-width: 96%; } }

        &-container {
            width: 100%;
            height: 100%;
            margin-left: auto;
            margin-right: auto;
            z-index: 1; }

        &-wrapper {
            margin: 0;
            padding: 0; }


        &-slide {
            text-align: center;
            height: 385px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 0 !important; //Swiper has a margin bug with column sliders and breakpoints. This is a workaround.
            margin-bottom: 25px;

            @media (max-width: 1600px) {
                height: unset; } } } }
